import React, { useEffect, useState } from "react";
import { ref, onValue } from "firebase/database";
import { database } from "../fireconfig"; // Import Firebase config
import AIPredictions from "./AIPredictions"; // Ensure AIPredictions component exists
import LeafletMap from "./LeafletMap";
import aiIcon from '../image/icon.png'; // AI icon path
import precautionIcon from '../image/pre.png'; // Precaution icon path

// List of gases with paths to Firebase data
const gases = [
  { name: "LPG", path: "/Car/Sensor/gasReading", threshold: 1100, max: 3000 },
  { name: "Ammonia", path: "/Sensor/Ammonia", threshold: 1000, max: 1500 },
  { name: "H2S", path: "/Sensor/H2S", threshold: 500, max: 1000 },
  { name: "SO2", path: "/Sensor/SO2", threshold: 750, max: 1500 },
  { name: "Cl2", path: "/Sensor/Cl2", threshold: 300, max: 500 },
  { name: "Hydrogen", path: "/Sensor/Hydrogen", threshold: 1500, max: 3000 },
  { name: "Acetylene", path: "/Sensor/Acetylene", threshold: 800, max: 1000 },
  { name: "Methane", path: "/Sensor/Methane", threshold: 2000, max: 3000 },
  { name: "Oxygen", path: "/Sensor/Oxygen", threshold: 23000, max: 25000 },
  { name: "CO", path: "/Sensor/CO", threshold: 1000, max: 2000 },
];

const GasDetection = () => {
  const [gasLevels, setGasLevels] = useState({});
  const [notificationsSent, setNotificationsSent] = useState({});
  const [gasReading, setGasReading] = useState(null);
  const [showAIPredictions, setShowAIPredictions] = useState(false);

  // UseEffect to listen for Firebase updates and trigger welcome message
  useEffect(() => {
    // Check if welcome message has been played
    const hasWelcomed = localStorage.getItem("hasWelcomed");

    if (!hasWelcomed) {
      // Play welcome message and set flag in localStorage
      const welcomeMessage = "Welcome home, sir";
      const speakMessage = (message) => {
        const synth = window.speechSynthesis;
        const utterance = new SpeechSynthesisUtterance(message);
        synth.speak(utterance);
      };
      speakMessage(welcomeMessage);

      // Set the welcome flag in localStorage
      localStorage.setItem("hasWelcomed", "true");
    }

    // Request permission for notifications
    if (Notification.permission !== "granted") {
      Notification.requestPermission();
    }

    // Listen for changes in gas sensor data
    gases.forEach((gas) => {
      const gasRef = ref(database, gas.path);
      onValue(gasRef, (snapshot) => {
        const level = snapshot.val();
        setGasLevels((prev) => ({ ...prev, [gas.name]: level }));
        checkThreshold(gas.name, level, gas.threshold);
      });
    });

    // Listen for gas reading updates
    const gasReadingRef = ref(database, "/Car/Sensor/gasReading");
    onValue(gasReadingRef, (snapshot) => {
      const reading = snapshot.val();
      setGasReading(reading);
    });
  }, []);

  // Function to check if gas level exceeds threshold
  const checkThreshold = (gasName, gasLevel, threshold) => {
    if (gasLevel > threshold && !notificationsSent[gasName]) {
      // Send notification if threshold exceeded
      if (Notification.permission === "granted") {
        new Notification(`⚠️ Warning: High ${gasName} Level!`, {
          body: `The ${gasName} level has reached ${gasLevel}, which is above the safe threshold.`,
        });
      }
      setNotificationsSent((prev) => ({ ...prev, [gasName]: true }));
    } else if (gasLevel <= threshold) {
      setNotificationsSent((prev) => ({ ...prev, [gasName]: false }));
    }
  };

  return (
    <div style={{ backgroundColor: "#ffffff", color: "#fff", height: "100vh", fontFamily: "'Roboto', sans-serif", paddingBottom: "20px" }}>
      {/* Gas Detection Title Section */}
      <div
        style={{
          backgroundImage: "linear-gradient(135deg, #00f7ff, #9b59b6, #ff2b96)",
          color: "white",
          padding: "20px 40px",
          borderRadius: "25px",
          textAlign: "center",
          boxShadow: "0 0 25px rgba(0, 255, 255, 0.5)",
          margin: "20px",
          animation: "glow 1.5s infinite alternate",
        }}
      >
        <h1 style={{ fontSize: "36px", marginBottom: "10px", textShadow: "0 0 10px rgba(0, 255, 255, 0.7)" }}>Gas Detection Levels</h1>

        {/* Gas Level Display */}
        <div style={{ display: "flex", flexWrap: "wrap", justifyContent: "center", gap: "20px" }}>
          {gases.map((gas) => (
            <div key={gas.name} style={{ padding: "15px", borderRadius: "15px", backgroundColor: "#2d2d2d", boxShadow: "0 0 15px rgba(0, 255, 255, 0.6)", width: "250px" }}>
              <p style={{ fontSize: "20px", color: "#33d9ff" }}>
                <strong>{gas.name} Level:</strong> {gasLevels[gas.name] || "Loading..."}
              </p>
              <meter
                min="0"
                max={gas.max}
                value={gasLevels[gas.name] || 0}
                optimum="100"
                low={gas.threshold * 0.5}
                high={gas.threshold}
                style={{
                  width: "100%",
                  height: "20px",
                  borderRadius: "15px",
                  backgroundColor: "#333",
                  boxShadow: "0 0 10px rgba(0, 255, 255, 0.6)",
                }}
              ></meter>
            </div>
          ))}
        </div>
      </div>

      {/* Current Gas Reading Display */}
      <div style={{ padding: "10px", borderRadius: "15px", backgroundColor: "#2d2d2d", color: "#fff", textAlign: "center", marginTop: "20px" }}>
        <h2>Current Gas Reading: {gasReading !== null ? gasReading : "Loading..."}</h2>
      </div>

      {/* Map Component */}
      <h2>Real-Time GPS Location</h2>

      {/* Precaution Icon Button Positioned on Left */}
      <a
        href="https://precaution-a5br.vercel.app"
        target="_blank"
        rel="noopener noreferrer"
        style={{
          position: "fixed",
          bottom: "20px",
          left: "20px", // Position on the left side
          backgroundColor: "#333",
          borderRadius: "50%",
          padding: "10px",
          boxShadow: "0 0 10px rgba(0, 255, 255, 0.6)",
          cursor: "pointer",
          zIndex: 10,
        }}
      >
        <img
          src={precautionIcon} // Precaution icon path
          alt="Precaution Icon"
          style={{ width: "40px", height: "40px" }}
        />
      </a>

      {/* AI Icon Button Positioned on Right */}
      <div
        style={{
          position: "fixed",
          bottom: "20px",
          right: "20px", // Position on the right side
          backgroundColor: "#333",
          borderRadius: "50%",
          padding: "10px",
          boxShadow: "0 0 10px rgba(0, 255, 255, 0.6)",
          cursor: "pointer",
          zIndex: 10,
        }}
        onClick={() => setShowAIPredictions(!showAIPredictions)}
      >
        <img
          src={aiIcon} // AI icon path
          alt="AI Icon"
          style={{ width: "40px", height: "40px" }}
        />
      </div>

      {/* AI Predictions Box */}
      {showAIPredictions && (
        <div
          style={{
            position: "fixed",
            bottom: "80px",
            right: "20px", // Align with the AI icon on the right
            overflow: "hidden",
            width: "300px",
            backgroundColor: "#2d2d2d",
            borderRadius: "15px",
            padding: "15px",
            boxShadow: "0 0 20px rgba(0, 255, 255, 0.8)",
            zIndex: 10,
          }}
        >
          <AIPredictions />
        </div>
      )}
    </div>
  );
};

// Keyframes for glowing animation
const styleSheet = document.styleSheets[0];
styleSheet.insertRule(`
  @keyframes glow {
    0% { box-shadow: 0 0 10px rgba(0, 255, 255, 0.8), 0 0 20px rgba(0, 255, 255, 0.6); }
    100% { box-shadow: 0 0 30px rgba(0, 255, 255, 1), 0 0 50px rgba(0, 255, 255, 0.8); }
  }
`, styleSheet.cssRules.length);

export default GasDetection;
