import React, { useState } from "react";
import GasDetection from "./components/GasDetection";
import VehicleControl from "./components/RobotControl";
import AIPredictions from "./components/AIPredictions";
import "./App.css";
import LeafletMap from "./components/LeafletMap";

function App() {
  const [activeTab, setActiveTab] = useState("Gas Detection");

  return (
    <div className="App">
      <header className="header">
        <h1>TOXIC AND EXPLOSIVE GAS DETECTION ROBOT</h1>
      </header>

      <div className="tabs">
        <button
          className={activeTab === "Gas Detection" ? "active" : ""}
          onClick={() => setActiveTab("Gas Detection")}
        >
          Gas levels
        </button>
        <button
          className={activeTab === "Vehicle Control" ? "active" : ""}
          onClick={() => setActiveTab("Vehicle Control")}
        >
          Robot Control
        </button>
        <button
          className={activeTab === "AI Predictions" ? "active" : ""}
          onClick={() => setActiveTab("AI Predictions")}
        >
          Vision
        </button>
      </div>

      <main className="content">
        {activeTab === "Gas Detection" && <GasDetection />}
        {activeTab === "Vehicle Control" && <VehicleControl />}
        {activeTab === "AI Predictions" && <AIPredictions />}
      </main>

    </div>
  );
}

export default App;
