import React, { useEffect, useState } from 'react';
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet';
import L from 'leaflet';
import 'leaflet/dist/leaflet.css';
import { initializeApp } from "firebase/app";
import { getDatabase, ref, onValue } from "firebase/database";

// Firebase configuration (replace with your own config)
const firebaseConfig = {
  apiKey: "AIzaSyATV1e7uytyRCnRG6haEFGO9gsz5N6P7mo",
  authDomain: "lpgdetectionllp.firebaseapp.com",
  databaseURL: "https://lpgdetectionllp-default-rtdb.asia-southeast1.firebasedatabase.app",
  projectId: "lpgdetectionllp",
  storageBucket: "lpgdetectionllp.firebasestorage.app",
  messagingSenderId: "95257927575",
  appId: "1:95257927575:web:2469bad3f59d277083aed2"
};

// Initialize Firebase
const firebaseApp = initializeApp(firebaseConfig);
const database = getDatabase(firebaseApp);

// Custom marker icon (optional)
const markerIcon = new L.Icon({
  iconUrl: 'https://leafletjs.com/examples/custom-icons/leaf-red.png',
  iconSize: [25, 41],
  iconAnchor: [12, 41],
});

const LeafletMap = () => {
  const [coordinates, setCoordinates] = useState({ lat: 0, lng: 0 });

  useEffect(() => {
    // Reference to latitude and longitude in Firebase Realtime Database
    const latRef = ref(database, 'Car/GPS/latitude');
    const lngRef = ref(database, 'Car/GPS/longitude');

    // Fetch latitude
    onValue(latRef, (snapshot) => {
      const lat = snapshot.val();
      if (lat !== null) {
        setCoordinates((prevCoords) => ({ ...prevCoords, lat }));
      }
    });

    // Fetch longitude
    onValue(lngRef, (snapshot) => {
      const lng = snapshot.val();
      if (lng !== null) {
        setCoordinates((prevCoords) => ({ ...prevCoords, lng }));
      }
    });
  }, []);

  return (
    <div>
      <h3>Leaflet Map with Firebase Coordinates</h3>
      <MapContainer center={[coordinates.lat, coordinates.lng]} zoom={13} style={{ height: "400px", width: "100%" }}>
        <TileLayer
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
        />
        <Marker position={[coordinates.lat, coordinates.lng]} icon={markerIcon}>
          <Popup>
            Coordinates: ({coordinates.lat}, {coordinates.lng})
          </Popup>
        </Marker>
      </MapContainer>
    </div>
  );
};

export default LeafletMap;
