import React, { useEffect, useState } from "react";
import { ref, set, onValue } from "firebase/database";
import { database } from "../fireconfig"; // Import the shared Firebase config

const RobotControl = () => {
  const [speed, setSpeed] = useState(255);
  const [gps, setGps] = useState({ latitude: "--", longitude: "--" });
  const [map, setMap] = useState(null);
  const [marker, setMarker] = useState(null);

  // Send command to the database
  const sendCommand = (command) => {
    console.log(command);
    set(ref(database, "/car/control"), command);
  };
  sendCommand("hello");

  // Send speed to the database
  const sendSpeed = (speed) => {
    set(ref(database, "/car/speed"), parseInt(speed));
  };

  // Initialize Google Map
  useEffect(() => {
    const initMap = () => {
      const initialPosition = { lat: 0, lng: 0 };
      const mapInstance = new window.google.maps.Map(document.getElementById("map"), {
        zoom: 15,
        center: initialPosition,
      });
      const markerInstance = new window.google.maps.Marker({
        position: initialPosition,
        map: mapInstance,
        title: "Robo Car Location",
      });

      setMap(mapInstance);
      setMarker(markerInstance);
    };

    if (window.google && window.google.maps) {
      initMap();
    }
  }, []);

  // Update map location
  const updateMap = (lat, lng) => {
    const position = { lat, lng };
    if (map && marker) {
      map.setCenter(position);
      marker.setPosition(position);
    }
  };

  // Listen for GPS data
  useEffect(() => {
    const gpsRef = ref(database, "/car/gps");
    const unsubscribe = onValue(gpsRef, (snapshot) => {
      const gpsData = snapshot.val();
      if (gpsData) {
        const { latitude, longitude } = gpsData;
        setGps({ latitude: latitude.toFixed(6), longitude: longitude.toFixed(6) });
        updateMap(latitude, longitude); // Call updateMap with updated GPS data
      }
    });

    // Cleanup the listener on component unmount
    return () => {
      unsubscribe(); // Unsubscribe to the Firebase listener when the component is unmounted
    };
  }, [map, marker]);  // Add map and marker as dependencies

  return (
    <div style={{ backgroundColor: "#ffffff", height: "100vh", fontFamily: "'Roboto', sans-serif", paddingBottom: "20px", color: "#fff" }}>
      <div 
        style={{
          backgroundImage: "linear-gradient(135deg, #00f7ff, #9b59b6, #ff2b96)",
          color: "white", 
          padding: "20px 40px", 
          borderRadius: "25px", 
          textAlign: "center", 
          boxShadow: "0 0 25px rgba(0, 255, 255, 0.5)",
          margin: "20px",
          animation: "glow 1.5s infinite alternate",
        }}
      >
        <h1 style={{ fontSize: "36px", marginBottom: "10px", textShadow: "0 0 10px rgba(0, 255, 255, 0.7)" }}>Robo Car Control</h1>

        {/* Control Buttons Layout */}
        <div style={{
          display: "grid",
          gridTemplateColumns: "1fr 50px 1fr",
          gridTemplateRows: "50px 50px 50px",
          gap: "20px",
          alignItems: "center",
          justifyItems: "center",
          marginBottom: "20px",
        }}>
          <button onClick={() => sendCommand("forward")} style={{ gridColumn: "2", gridRow: "1", ...buttonStyle }}>↑</button>
          <button onClick={() => sendCommand("left")} style={{ gridColumn: "1", gridRow: "2", ...buttonStyle }}>←</button>
          <button onClick={() => sendCommand("stop")} style={{ gridColumn: "2", gridRow: "2", ...buttonStyle }}>■</button>
          <button onClick={() => sendCommand("right")} style={{ gridColumn: "3", gridRow: "2", ...buttonStyle }}>→</button>
          <button onClick={() => sendCommand("backward")} style={{ gridColumn: "2", gridRow: "3", ...buttonStyle }}>↓</button>
        </div>
        
        {/* Speed Control */}
        <div style={{ marginBottom: "20px" }}>
          <label style={{ fontSize: "18px", fontWeight: "bold" }}>Speed Control</label><br />
          <input 
            type="range" 
            min="0" 
            max="255" 
            value={speed} 
            onChange={(e) => { setSpeed(e.target.value); sendSpeed(e.target.value); }} 
            style={inputStyle} 
          />
          <div style={{ fontSize: "18px", marginTop: "10px" }}>Speed: {speed}</div>
        </div>
      </div>

      {/* GPS Location and Map */}
      <div style={{
        maxWidth: "900px",
        margin: "auto",
        backgroundColor: "#1c1c1c",
        borderRadius: "20px",
        padding: "20px",
        boxShadow: "0 0 30px rgba(0, 255, 255, 0.6)",
        animation: "glow 1.5s infinite alternate",
      }}>
        <h2 style={{ textAlign: "center", fontSize: "28px", color: "#33d9ff", textShadow: "0 0 10px rgba(0, 255, 255, 0.7)" }}>GPS Location</h2>
        <div style={{ fontSize: "18px", color: "#ccc" }}>
          <p><strong>Latitude:</strong> {gps.latitude}</p>
          <p><strong>Longitude:</strong> {gps.longitude}</p>
        </div>
        <div id="map" style={{ height: "400px", width: "100%", borderRadius: "10px", marginTop: "20px" }}></div>
      </div>
    </div>
  );
};

// Button Styling with Holographic Effects and Hover Animations
const buttonStyle = {
  backgroundColor: "#33d9ff", 
  color: "white", 
  border: "none", 
  padding: "20px", 
  fontSize: "20px", 
  margin: "10px", 
  borderRadius: "50%", 
  cursor: "pointer", 
  boxShadow: "0 0 20px rgba(51, 217, 255, 0.8)", 
  transition: "transform 0.3s ease, box-shadow 0.3s ease",
  outline: "none",
  width: "70px",
  height: "70px",
  textShadow: "0 0 15px rgba(51, 217, 255, 1)",
};

const inputStyle = {
  width: "100%",
  padding: "15px",
  fontSize: "16px",
  borderRadius: "30px",
  backgroundColor: "#2d2d2d",
  border: "2px solid #33d9ff",
  color: "#33d9ff",
  outline: "none",
  transition: "border-color 0.3s ease",
};

// Keyframes for glowing animation
const styleSheet = document.styleSheets[0];
styleSheet.insertRule(`
  @keyframes glow {
    0% { box-shadow: 0 0 10px rgba(0, 255, 255, 0.8), 0 0 20px rgba(0, 255, 255, 0.6); }
    100% { box-shadow: 0 0 30px rgba(0, 255, 255, 1), 0 0 50px rgba(0, 255, 255, 0.8); }
  }
`, styleSheet.cssRules.length);

export default RobotControl;
