import React, { useState, useEffect } from "react";
import "./AIPredictions.css"; // Make sure to import the CSS for styles
import { ref, set } from "firebase/database"; // Firebase methods
import { database } from "../fireconfig"; // Import your Firebase config

function AIPredictions() {
  const [transcript, setTranscript] = useState("");
  const [inputText, setInputText] = useState("");
  const [isListening, setIsListening] = useState(false);
  const [loading, setLoading] = useState(false);
  const [searchResult, setSearchResult] = useState("");
  const [reminders, setReminders] = useState([]);
  const [weatherInfo, setWeatherInfo] = useState("");
  const [synthVoice, setSynthVoice] = useState(null); // State for the voice
  const [voices, setVoices] = useState([]); // State to hold the list of voices

  useEffect(() => {
    const loadVoices = () => {
      const availableVoices = window.speechSynthesis.getVoices();
      setVoices(availableVoices);
      const defaultVoice = availableVoices.find(voice => voice.name.includes("Google") || voice.name.includes("US")) || availableVoices[0];
      setSynthVoice(defaultVoice);
    };

    window.speechSynthesis.onvoiceschanged = loadVoices;
    loadVoices();

    return () => {
      window.speechSynthesis.onvoiceschanged = null;
    };
  }, []);

  const startListening = () => {
    if (!("SpeechRecognition" in window || "webkitSpeechRecognition" in window)) {
      alert("Your browser doesn't support speech recognition.");
      return;
    }

    const recognition = new (window.SpeechRecognition || window.webkitSpeechRecognition)();
    recognition.continuous = false;
    recognition.interimResults = false;
    recognition.lang = "en-US";

    recognition.onstart = () => {
      console.log("Voice recognition started...");
      setIsListening(true);
    };

    recognition.onresult = (event) => {
      const userInput = event.results[0][0].transcript.toLowerCase().trim();
      setTranscript(userInput);
      setInputText(userInput);
      takeCommand(userInput);
    };

    recognition.onerror = (event) => {
      console.error("Recognition error:", event);
      setIsListening(false);
    };

    recognition.onend = () => {
      console.log("Voice recognition ended.");
      setIsListening(false);
    };

    recognition.start();
  };

  const speakResponse = (text) => {
    if ("speechSynthesis" in window) {
      const utterance = new SpeechSynthesisUtterance(text);
      utterance.lang = "en-US";
      if (synthVoice) {
        utterance.voice = synthVoice;
      }
      window.speechSynthesis.speak(utterance);
    } else {
      alert("Your browser doesn't support speech synthesis.");
    }
  };

  const sendCommand = (command) => {
    set(ref(database, "/car/control"), command);
  };

  const takeCommand = async (message) => {
    if (message.includes("move forward") || message.includes("go forward")) {
      sendCommand("forward");
      speakResponse("Moving forward.");
    } else if (message.includes("move backward") || message.includes("go backward")) {
      sendCommand("backward");
      speakResponse("Moving backward.");
    } else if (message.includes("turn left")) {
      sendCommand("left");
      speakResponse("Turning left.");
    } else if (message.includes("turn right")) {
      sendCommand("right");
      speakResponse("Turning right.");
    } else if (message.includes("stop")) {
      sendCommand("stop");
      speakResponse("Stopping.");
    } else if (message.includes("change voice")) {
      changeVoice(); // Change voice command if detected
    } else if (message.includes("what's the weather")) {
      const city = message.replace("what's the weather in", "").trim();
      await fetchWeather(city);
    } else if (message.includes("what time is it")) {
      const currentTime = new Date().toLocaleTimeString();
      speakResponse(`The current time is ${currentTime}.`);
    } else if (message.includes("remind me to")) {
      const reminder = message.replace("remind me to", "").trim();
      setReminders([...reminders, reminder]);
      speakResponse(`Reminder set for ${reminder}.`);
    } else if (message.includes("what are my reminders")) {
      const remindersList = reminders.join(", ") || "You have no reminders.";
      speakResponse(remindersList);
    } else if (message.includes("tell me a joke")) {
      speakResponse("Why don’t skeletons fight each other? They don’t have the guts.");
    } else if (message.includes("news")) {
      await fetchNews();
    } else if (message.includes("help")) {
      speakResponse("You can ask me to move forward, backward, turn left or right, get weather info, tell the time, set reminders, tell jokes, or fetch news.");
    } else {
      await fetchSearchResults(message);
    }
  };

  const changeVoice = () => {
    const currentIndex = voices.indexOf(synthVoice);
    const nextIndex = (currentIndex + 1) % voices.length;
    setSynthVoice(voices[nextIndex]);
    speakResponse(`Voice changed to ${voices[nextIndex].name}.`);
  };

  const fetchSearchResults = async (query) => {
    setLoading(true);
    const url = `https://google-search74.p.rapidapi.com/?query=${encodeURIComponent(query)}&limit=10&related_keywords=true`;
    const options = {
      method: 'GET',
      headers: {
        'x-rapidapi-key': '512c53ae49msh5ca8ed20490e070p176665jsn7413a8765253', // Replace this with your actual RapidAPI key
        'x-rapidapi-host': 'google-search74.p.rapidapi.com'
      }
    };

    try {
      const response = await fetch(url, options);
      const data = await response.json();

      if (data.results && data.results.length > 0) {
        const topResult = data.results[0];
        const resultText = topResult.description || "No description available.";
        setSearchResult(resultText);
        speakResponse(resultText);
      } else {
        setSearchResult("No results found.");
        speakResponse("I couldn't find any results for your search.");
      }
    } catch (error) {
      console.error("Error fetching search results:", error);
      setSearchResult("Error fetching results.");
      speakResponse("There was an error fetching results.");
    } finally {
      setLoading(false);
    }
  };

  const fetchWeather = async (city) => {
    setLoading(true);
    const apiKey = 'YOUR_OPENWEATHERMAP_API_KEY';
    const url = `https://api.openweathermap.org/data/2.5/weather?q=${encodeURIComponent(city)}&appid=${apiKey}&units=metric`;

    try {
      const response = await fetch(url);
      const data = await response.json();

      if (data.main) {
        const { temp } = data.main;
        const weatherDescription = data.weather[0].description;
        const weatherText = `The current temperature in ${city} is ${temp} degrees Celsius with ${weatherDescription}.`;
        setWeatherInfo(weatherText);
        speakResponse(weatherText);
      } else {
        speakResponse("I couldn't find the weather for that location.");
      }
    } catch (error) {
      console.error("Error fetching weather data:", error);
      speakResponse("There was an error fetching the weather.");
    } finally {
      setLoading(false);
    }
  };

  const fetchNews = async () => {
    setLoading(true);
    const url = `https://newsapi.org/v2/top-headlines?country=us&apiKey=YOUR_NEWS_API_KEY`;

    try {
      const response = await fetch(url);
      const data = await response.json();
      
      if (data.articles && data.articles.length > 0) {
        const headlines = data.articles.slice(0, 5).map(article => article.title).join(", ");
        speakResponse(`Here are the top news headlines: ${headlines}`);
      } else {
        speakResponse("Sorry, I couldn't fetch the news right now.");
      }
    } catch (error) {
      console.error("Error fetching news:", error);
      speakResponse("There was an error fetching the news.");
    } finally {
      setLoading(false);
    }
  };

  const handleInputChange = (e) => {
    setInputText(e.target.value);
  };

  const handleInputSubmit = (e) => {
    e.preventDefault();
    takeCommand(inputText);
  };

  return (
    <div className="ai-predictions-container">
      <h1>AI Predictions</h1>
      <button onClick={startListening} disabled={isListening}>
        {isListening ? "Listening..." : "Start Listening"}
      </button>
      <div>
        <input
          type="text"
          value={inputText}
          onChange={handleInputChange}
          placeholder="Type a command"
        />
        <button onClick={handleInputSubmit} disabled={loading}>
          Submit Command
        </button>
      </div>
      {loading && <p>Loading...</p>}
      <p>Transcript: {transcript}</p>
      <p>Search Result: {searchResult}</p>
      <p>Weather: {weatherInfo}</p>
      <ul>
        {reminders.map((reminder, index) => (
          <li key={index}>{reminder}</li>
        ))}
      </ul>
    </div>
  );
}

export default AIPredictions;
